import axios, { CancelToken } from 'axios';

console.log('333', process.env.NODE_ENV);

const host =
    process.env.NODE_ENV === 'development'
        ? 'http://0.0.0.0:8000'
        : 'https://i18n-manager.herokuapp.com';

const apiHost = `${host}/api/v1`;

const instance = axios.create({
    baseURL: apiHost,
    responseType: 'json',
    headers: {
        Authorization: 'Token ' + localStorage.getItem('token'),
        // 'Access-Control-Allow-Origin': '*',
    },
    // headers: {
    //     // 'Access-Control-Allow-Origin': '*',
    // },
    // cancelToken: new CancelToken(function(cancel) {
    //     console.warn('axios request was manualy closed ');
    // }),
});

// const token = localStorage.getItem('token');
// instance.defaults.headers.common['Authorization'] = token;

const unsecureInstance = axios.create({
    baseURL: apiHost,
    responseType: 'json',
    // cancelToken: new CancelToken(function(cancel) {
    //     console.warn('axios request was manualy closed ');
    // }),
});

async function getDocuments() {
    try {
        const { data } = await instance.get('/documents/', {
            headers: {
                Authorization: 'Token ' + localStorage.getItem('token'),
            },
        });
        return data;
    } catch (e) {
        console.error(e);
    }
}

async function getDocument(id) {
    try {
        const { data } = await instance.get(`/documents/${id}`, {
            headers: {
                Authorization: 'Token ' + localStorage.getItem('token'),
            },
        });
        return data;
    } catch (e) {
        console.error(e);
    }
}

async function removeDocumentById(id) {
    try {
        const { data } = await instance.delete(`/documents/${id}`, {
            headers: {
                Authorization: 'Token ' + localStorage.getItem('token'),
            },
        });
        return data;
    } catch (e) {
        console.error(e);
    }
}

async function createDocumentItem(document = {}) {
    try {
        const { data } = await instance.post('/documents/', {
            params: document,
            headers: {
                Authorization: 'Token ' + localStorage.getItem('token'),
            },
        });
        return data;
    } catch (e) {
        console.error(e);
    }
}

async function getUser() {
    try {
        const { data } = await instance.get('/rest-auth/user/', {
            headers: {
                Authorization: 'Token ' + localStorage.getItem('token'),
            },
        });
        return data;
    } catch (e) {
        console.error(e);
    }
}

export {
    unsecureInstance,
    getDocuments,
    getUser,
    getDocument,
    removeDocumentById,
    createDocumentItem,
};
export default instance;
